import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {RouterModule, Routes} from '@angular/router';
import {AppComponent} from './app.component';


const routes: Routes = [
  {
    path: 'questionnaire',
    loadChildren: () => import('./questionnaire/questionnaire.module').then(mod => mod.QuestionnaireModule)
  }, {
    path: '',
    loadChildren: () => import('./start-page/start-page.module').then(mod => mod.StartPageModule)
  }, {
    path: 'fill',
    loadChildren: () => import('./answer/answer.module').then(mod => mod.AnswerModule)
  }
];

@NgModule({
  declarations: [],
  imports: [RouterModule.forRoot(routes, {enableTracing: false})],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
